import {Item, Checkbox, Form, Header} from 'semantic-ui-react';
import {FormattedMessage} from 'react-intl';
import { ParsedQuery } from 'query-string';
import * as queryString from 'query-string';
import { useHistory, useLocation } from 'react-router';

export enum ChartColors {
  NO_COLOR,
  COLOR_BY_SEX,
  COLOR_BY_GENERATION,
}

export enum ChartViews {
  HOURGLASS,
  RELATIVES,
  FANCY
}

export interface Config {
  color: ChartColors;
  view: ChartViews;
}

export const DEFALUT_CONFIG: Config = {
  color: ChartColors.COLOR_BY_GENERATION,
  view: ChartViews.HOURGLASS
};

const COLOR_ARG = new Map<string, ChartColors>([
  ['n', ChartColors.NO_COLOR],
  ['g', ChartColors.COLOR_BY_GENERATION],
  ['s', ChartColors.COLOR_BY_SEX],
]);
const COLOR_ARG_INVERSE = new Map<ChartColors, string>();
COLOR_ARG.forEach((v, k) => COLOR_ARG_INVERSE.set(v, k));

export function argsToConfig(args: ParsedQuery<any>): Config {
  const getParam = (name: string) => {
    const value = args[name];
    return typeof value === 'string' ? value : undefined;
  };

  return {
    color: COLOR_ARG.get(getParam('c') ?? '') ?? DEFALUT_CONFIG.color,
    view: DEFALUT_CONFIG.view
  };
}

export function configToArgs(config: Config): ParsedQuery<any> {
  return {c: COLOR_ARG_INVERSE.get(config.color)};
}

export function ConfigPanel(props: {
  config: Config;
  onChange: (config: Config) => void;
}) {
  const history = useHistory();
  const location = useLocation();

  function changeView(view: string) {
    const search = queryString.parse(location.search);
    if (search.view !== view) {
      search.view = view;
      location.search = queryString.stringify(search);
      history.push(location);
    }
  }

  return (
    <Form className="details">
      <Item.Group>
        <Item>
          <Item.Content>
            <Header sub>
              <FormattedMessage id="config.colors" defaultMessage="Colors" />
            </Header>
            <Form.Field className="no-margin">
              <Checkbox
                radio
                label={
                  <FormattedMessage
                    tagName="label"
                    id="config.colors.NO_COLOR"
                    defaultMessage="none"
                  />
                }
                name="checkboxRadioGroup"
                value="none"
                checked={props.config.color === ChartColors.NO_COLOR}
                onClick={() => props.onChange({ color: ChartColors.NO_COLOR, view: props.config.view })}
              />
            </Form.Field>
            <Form.Field className="no-margin">
              <Checkbox
                radio
                label={
                  <FormattedMessage
                    tagName="label"
                    id="config.colors.COLOR_BY_GENERATION"
                    defaultMessage="by generation"
                  />
                }
                name="checkboxRadioGroup"
                value="generation"
                checked={props.config.color === ChartColors.COLOR_BY_GENERATION}
                onClick={() =>
                  props.onChange({ color: ChartColors.COLOR_BY_GENERATION, view: props.config.view})
                }
              />
            </Form.Field>
            <Form.Field className="no-margin">
              <Checkbox
                radio
                label={
                  <FormattedMessage
                    tagName="label"
                    id="config.colors.COLOR_BY_SEX"
                    defaultMessage="by sex"
                  />
                }
                name="checkboxRadioGroup"
                value="gender"
                checked={props.config.color === ChartColors.COLOR_BY_SEX}
                onClick={() =>
                  props.onChange({ color: ChartColors.COLOR_BY_SEX, view: props.config.view})
                }
              />
            </Form.Field>
          </Item.Content>
        </Item>
        <Item>
          <Item.Content>
            <Header sub>
              <FormattedMessage id="menu.view" defaultMessage="View" />
            </Header>
            <Form.Field className="no-margin">
              <Checkbox
                radio
                label={
                  <FormattedMessage
                    tagName="label"
                    id="menu.hourglass"
                    defaultMessage="Hourglass chart"
                  />
                }
                name="checkboxRadioGroup"
                value="hourglass"
                checked={props.config.view === ChartViews.HOURGLASS}
                onClick={() => { props.config.view = ChartViews.HOURGLASS; changeView('hourglass') }}
              />
            </Form.Field>
            <Form.Field className="no-margin">
              <Checkbox
                radio
                label={
                  <FormattedMessage
                    tagName="label"
                    id="menu.relatives"
                    defaultMessage="All relatives"
                  />
                }
                name="checkboxRadioGroup"
                value="relatives"
                checked={props.config.view === ChartViews.RELATIVES}

                onClick={() => { props.config.view = ChartViews.RELATIVES; changeView('relatives') }}
              />
            </Form.Field>
            <Form.Field className="no-margin">
              <Checkbox
                radio
                label={
                  <FormattedMessage
                    tagName="label"
                    id="menu.fancy"
                    defaultMessage="Fancy tree (experimental)"
                  />
                }
                name="checkboxRadioGroup"
                value="fancy" checked={props.config.view === ChartViews.FANCY}
                onClick={() => { props.config.view = ChartViews.FANCY; changeView('fancy') }}
              />
            </Form.Field>
          </Item.Content>
          </Item>
      </Item.Group>
    </Form>
  );
}
