function Login() {
  function GetPassphrase()
  {
    const passphrase = prompt("Jelsz a csaldfhoz:");
    if (passphrase)
    {
      sessionStorage.setItem("passphrase", passphrase);
      window.location.assign("https://patayg.github.io/luca/index.html#/view");
    }
    return true;
  }

  try
  {
    const hashData = sessionStorage.getItem("passphrase");
    if (!hashData)
    {
      return (
        <>
          <div><b>
            <Link to="#" onClick={() => GetPassphrase()}>
               A tovbblpshez krlek, kattints ide s add meg a jelszt!
            </Link>
         </b></div>
        </>
      );
    }
  }
  catch (e)
  {
      console.warn('Failed to load data from session storage: ' + e);
  }
  return null;
}

function BackToMain() {
  const history = useHistory();
  try
  {
    const hashData = sessionStorage.getItem("passphrase");
    if (hashData)
    {
      return (
        <>
          <div><b>
            <Link to="#" onClick={() => history.goBack()}>
              Vissza a foldalra
            </Link>
          </b></div>
        </>
      );
    }
  }
  catch (e)
  {
    console.warn('Failed to load data from session storage: ' + e);
  }
}



function Contents() {
  const [changelog, setChangelog] = useState('');
  useEffect(() => {
    (async () => {
      setChangelog(await getChangelog(1));
      updateSeenVersion();
    })();
  });

  return (
    <>
      <p>
        Ez a Luca csald rokonsgi hlzata. (csaldfnak egy kicsit taln tl gas-bogas)
      </p>
      <p>
      {Login()}
      </p>
      <p>
      Nagyon rlk minden kiegsztsnek, javtsnak. A mdostsokat gy juttathatjtok el hozzm:
      <ul>
        <li>email: gergely.patay kukac gmail.com
        </li>
        <li>Facebook: Patay Gergely <a href="https://www.facebook.com/gergely.patay">https://www.facebook.com/gergely.patay</a>
        </li>
        </ul>
      </p>

      <p>
        A megjelent a <a href="https://github.com/PeWu/topola-viewer">Topola Geneology Viewer</a> mdostott verzijt hasznlja.
      </p>

      <p>
        {BackToMain()}
      </p>
    </>
  );
}




/** The intro page. */
export function Intro() {
  return (
    <div id="content">
      <div className="backgroundImage" />
      <Card className="intro">
        <Card.Content as={Media} greaterThanOrEqual="large">
          <Card.Header>
            <FormattedMessage
              id="intro.title"
              defaultMessage="Luca Family Tree"
            />
          </Card.Header>
        </Card.Content>
        <Card.Content>
          <Grid as={Media} greaterThanOrEqual="large">
            <Grid.Row>
              <Grid.Column width={5}>
                <Image src={logo} alt="Topola logo" />
              </Grid.Column>
              <Grid.Column width={11}>
                <Contents />
              </Grid.Column>
            </Grid.Row>
          </Grid>
          <Media at="small">
            <Image
              src={logo}
              alt="Topola logo"
              centered={true}
              size="tiny"
              className="blockImage"
            />
            <Contents />
          </Media>
        </Card.Content>
      </Card>
    </div>
  );
}
